import { ProductData } from '@data/Product/Product.data';
import { ProductPaginationData } from '@data/Product/ProductPagination.data';
import { Product } from '@interface/Product/Product.interface';
import { ProductPagination } from '@interface/Product/ProductPagination.interface';
import { defineComponent } from '@vue/runtime-core';
import axios from 'axios';
import $ from 'jquery';
require('bootstrap');

/** 
 * @description Componente que muestra la lista de productos registrados en el servidor, mediante el uso de una tabla
 * @class ProductTableComponent
 * @example <product-table-component></product-table-component>
*/
export default defineComponent({
    /** 
     * {@link ProductComponent}, {@link SuccessAlertComponent}, {@link ErrorAlertComponent} ,{@link ConfirmationAlertComponent}
     * @member ProductTableComponent.components
    */
    components: {
        ProductComponent: require('@component/product/ProductComponent.vue').default,
        SuccessAlertComponent: require('@component/general/alert/SuccessAlertComponent.vue').default,
        ErrorAlertComponent: require('@component/general/alert/ErrorAlertComponent.vue').default,
        ConfirmationAlertComponent: require('@component/general/alert/ConfirmationAlertComponent/ConfirmationAlertComponent.vue').default
    },
    /** 
     * Propiedades que recibe el componente 
     * @member ProductTableComponent.props
     * @property {string} title (Opcional) Ttulo del componente a mostrar en la tabla
     * @property {boolean} productCategory (Obligatorio) Categora a la que pertenece el producto seleccionado (ciclica, histeroscopia, etc)
     * @property {string} id (Obligatorio) ID del componente
     * @property {string} enableActions Habilita el botn de creacin de producto, adems de habilitar la opcin de editar o ver el producto dentro de la tabla 
    */
    props: {
        title: {
            type: String,
            default: 'Producto'
        },
        productCategory: {
            type: String,
            default: 'ciclica'
        },
        id: {
            type: String,
            default: ''
        },
        enableActions: {
            type: Boolean,
            default: true
        }
    },
    /**
    * Variables del componente
    * @member ProductTableComponent.data
    * @property {ProductPagination} paginationData Guarda la informacin de los productos registrados en el sistema, de acuerdo a la categora a la que pertenecen
    * @property {number} paginationPages Guarda el nmero de pginas de userData para mostrar el nmero de pginas en la paginacin de la tabla
    * @property {number} paginationActive Guarda el nmero de pgina activa de la tabla
    * @property {string} query Guarda la bsqueda realizada en el input de bsqueda de la tabla
    * @property {boolean} activateSearch Activa o no la bsqueda desde el input de bsqueda de la tabla
    * @property {Product} productData Guarda los datos del producto seleccionado
    * @property {boolean} loading Activa o no la animacin de carga dentro de la tabla
    * @property {boolean} isNewProduct Permite al componente {@link ProductComponent} saber si la variable productData es nueva o existente
    * @property {Array<Object>} errors Guarda los mensajes de error provenientes del backend si existe un error en la peticin HTTP mediante axios, los cuales se muestran en el componente {@link ErrorAlertComponent}
    */
    data() {
        return {
            paginationData: ProductPaginationData,
            paginationPages: 0,
            paginationActive: 0,
            query: '',
            activateSearch: true,
            productData: ProductData,
            loading: true,
            isNewProduct: true,
            errors: [],
        };
    },
    /** 
     * Al iniciar el componente, se obtienen los datos de los {@link ProductTableComponent.getProductData|productos registrados de acuerdo a la categora seleccionada}
     * @member ProductTableComponent.mounted
    */
    mounted() {
        this.getProductData(1);
    },
    /**
    * Propiedades computadas del componente 
    * @member ProductTableComponent.computed
    * @property {boolean} showProductCode Muestra u oculta la columna de cdigo de proveedor en caso de existir o no dicho campo en algunos de los productos
    * @property {boolean} showQuantityAvailable Muestra u oculta la columna de la cantidad disponible del producto en caso de existir o no dicho campo en algunos de los productos
    * @property {boolean} showDiscount Muestra u oculta la columna del descuento del producto en caso de existir o no dicho campo en algunos de los productos
    */
    computed: {
        showProductCode(): boolean
        {
            return this.paginationData.data.filter(item => item.supplier_code !== null).length > 0 ? true: false;
        },
        showQuantityAvailable(): boolean
        {
            return this.paginationData.data.filter(item => item.quantity_available !== null).length > 0 ? true: false;
        },
        showDiscount(): boolean
        {
            return this.paginationData.data.filter(item => item.discount !== null).length > 0 ? true: false;
        }
    },
    methods: {
        /** 
         * Obtiene la lista de productos registrados en el sistema utilizando la paginacin provista por el backend.
         * Antes de realizar la peticin al servidor con la lista de productos registrados, se habilita la animacin de carga con la asignacin true de la variable loading, se verifica que la pgina guardada en la variable page sea mayor o igual a 1,
         * que la pgina sea menor o igual a la ltima pgina de la paginacin guardada en la variable
         * paginationData y que la pgina no sea la misma que la de la variable paginationActive.
         * Si la comprobacin es exitosa se realiza una peticin HTTP a la URI /productos/{categoria} con el parmetro de la pgina. Si la peticin es correcta se asigna la variable paginationData con la respuesta
         * del servidor, se asigna a la variabler paginationPages con los datos de la paginacin provista por el servidor y se deshabilita la animacin de carga mediante la asignacin false de la variable loading,
         * en caso contrario solo se deshabilita la animacin de la carga mediante la asignacin false de la variable loading
         * @param {number} page Nmero de la pgina
         * @function ProductTableComponent.getProductData
        */
        getProductData(page: number)
        {
            this.loading = true;
            if(page >= 1 && page <= this.paginationData.pagination.last_page && page !== this.paginationActive)
            {
                this.paginationActive = page;
                axios.get<ProductPagination>(`/productos/${this.productCategory}?page=${this.paginationActive}`)
                .then(response => {
                    this.paginationData = response.data;
                    this.paginationPages = response.data.pagination.last_page;
                    this.loading = false;
                })
                .catch(error => {
                    ;
                    this.loading = false;
                })
            }
        },
        /** 
         * Obtiene la lista de productos registrados en el sistema que coincidan con la bsqueda realizada por el usuario.
         * Antes de realizar la peticin al servidor con la lista de productos registrados, se habilita la animacin de carga con la asignacin true de la variable loading,
         * se asigna a la variable queryPagination el valor de la variable paginationActive si la variable query est vaco, en caso contrario se le asigna el 0.
         * Si la variable activateSeach es true o la longitud de la variable query es mayor a 0, se realizar una llamada a la URI /productos/{categoria} con el parmetro de la pgina y el contenido de la bsqueda.
         * Si la peticin es correcta se asigna la variable paginationData con la respuesta del servidor, se asigna a la variabler paginationPages con los datos de la paginacin provista por el servidor
         * y se deshabilita la animacin de carga mediante la asignacin false de la variable loading, en caso contrario solo se deshabilita la animacin de la carga mediante la asignacin false de la variable loading
         * @function ProductTableComponent.getProductDataQuery
        */
        getProductDataQuery()
        {
            this.loading = true;
            const queryPagination = this.query === '' ? this.paginationActive : 0;
            if(this.activateSearch || this.query.length > 0)
            {
                axios.get<ProductPagination>(`/productos/${this.productCategory}`, {
                    params: {
                        page: queryPagination,
                        query: this.query
                    }
                })
                .then(response => {
                    this.paginationData = response.data;
                    this.paginationPages = response.data.pagination.last_page;
                    this.loading = false;
                })
                .catch(error => {
                    this.loading = false;
                })
            }
            this.activateSearch = this.query === '' ? false : true;
        },
        /** 
         * Crea un nuevo producto, asignando datos vacos a la variable productData, junto con la asignacin true de la variable isNewProduct,
         * finalmente se muestra el componente {@link ProductComponent}
         * @function ProductTableComponent.createProduct
        */
        createProduct()
        {
            this.productData = ProductData;
            this.isNewProduct = true;
            $(`#productModal${this.id}`).modal('show');
        },
        /** 
         * Asigna los datos del producto seleccionado a la variable productData, junto con la asignacin false de la variable isNewProduct (para indicar que es un producto existente),
         * finalmente se muestra el componente {@link ProductComponent}
         * @function ProductTableComponent.createProduct
         * @param {Product} productData Datos del producto seleccionado
        */
        editProduct(productData: Product)
        {
            this.productData = {...productData};
            this.isNewProduct = false;
            $(`#productModal${this.id}`).modal('show');
        },
        /** 
         * Se elimina el producto seleccionado del servidor. Si la peticin se procesa correctamente, se muestra el componente {@link SuccessAlertComponent} con
         * un mensaje de xito, adems de eliminar el producto de la tabla al eliminarlo de la variable paginationData. En caso contrario se obtienen los errores
         * del servidor y se asigna a la variable errores, mostrando dichos errores en el componente {@link ErrorAlertComponent}
         * @function ProductTableComponent.deleteProduct
        */
        deleteProduct()
        {
            axios.delete<Product>(`/productos/${this.productData.id}`)
            .then(response => {
                $(`#productTableAlertSuccess${this.id}`).modal('show');
                this.paginationData.data = this.paginationData.data.filter(product => product.id !== this.productData.id);
            })
            .catch(error => {
                this.errors = error.response.data.errors;
                $(`#productTableAlertError${this.id}`).modal('show');
            })
        },
        /** 
         * Abre el componente {@link ConfirmationAlertComponent} con un mensaje de confirmacin, antes de eliminar un producto del servidor
         * @function ProductTableComponent.openDeleteConfirmation
        */
        openDeleteConfirmation(productData: Product)
        {
            this.productData = productData;
            $(`#productTableAlertConfirmation${this.id}`).modal('show');
        }
    },
})