<template>
    <div class="modal fade" :id="id" tabindex="-1" role="dialog" aria-labelledby="errorModal" aria-hidden="true">
        <div class="modal-dialog modal-sm modal-info modal-dialog-centered" role="document">
            <div class="modal-content modal-bg-danger modal-colored">
                <div class="modal-header ">
                    <h6 class="modal-title">{{ title }}</h6>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <img-component url="/svg/close.svg" alt="Cerrar" styleData="filter: invert(1);"></img-component>
                    </button>
                </div>
                <div class="modal-body mb-3">
                    <div class="modal-info-body d-flex mt-2" v-for="error in errors" :key="error">
                        <div class="modal-info-icon danger">
                            <img-component url="/svg/alert.svg" alt="Alerta" styleData="filter: invert(1);"></img-component>
                        </div>
                        <div class="modal-info-text text-white" v-for="errorInfo in error" :key="errorInfo">
                            {{ errorInfo }}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
/** 
 * @description Componente que muestra un mensaje de error, mediante el uso de un modal
 * @class ErrorAlertComponent
 * @example <error-alert-component :id="" :title="" :errors=""></error-alert-component>
*/
export default {
    /** 
     * Propiedades que recibe el componente 
     * @member ErrorAlertComponent.props
     * @property {string} id (Obligatorio) ID del componente
     * @property {string} title (Obligatorio) Ttulo del mensaje de error
     * @property {string[]} errors (Obligatorio) Lista de errores para mostrar en el cuerpo del componente
    */
    props: ['id', 'title', 'errors'],
    mounted:  function() {
    },
    watch:{
        errors() {
        }
    }
}
</script>
